<template>
  <div class="info base_width">
    <h3 class="header font-weight-normal">{{infomation.title}}</h3>
    <h5 class="tips">发布时间：{{infomation.publishTime}}</h5>
    <div class="content">
      <div class="box-header" v-html="infomation.content"></div>
    </div>

    <div class="infoPageBox clear_fix">
      <div @click="prevs()" class="infoBtn block_left">
        <span class="icon-left">
          <i class="iconfont hd_back_left_icon">&#xe60e;</i>

          <span class="block_right">上一篇</span>
        </span>
      </div>
      <div @click="next()" class="infoBtn block_right">
        <span class="icon-right">
          <span class="block_left">下一篇</span>

          <i class="iconfont hd_back_right_icon">&#xe60e;</i>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
export default {
  name: "News_Detail",
  data() {
    return {
      first: false,
      last: false,
      prevId: "",
      nextId: "",
      detailUrl: "",
      infomation: {},
      detailId: [],
      id: null,
      lock: false //点击上一篇下一篇加锁
    };
  },

  mounted: function() {
    this.getData(this.id);
  },

  methods: {
    getData(_id) {
      //通过id查询资讯详情

      this.id = this.$route.params.id;
      const that = this;
      this.$axios
        .post("/v1/information/queryInfoDynamicsById", {
          id: _id || that.id
        })
        .then(function(res) {
          console.log(res);
          that.infomation = res.data.infoDynamicsBean;
          that.getDataId();
          console.log(that.infomation);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //获取数组ID中元素的位置
    getIndexOf(arr, item) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == item) {
          return i;
        }
      }
    },
    //获取上一篇和下一篇的id值
    getId(arr, item) {
      //获取所有资讯id数组
      let _indexOf = this.getIndexOf(arr, item);
      if (_indexOf < arr.length - 1 && _indexOf > 0) {
        this.nextId = arr[_indexOf + 1];
        this.prevId = arr[_indexOf - 1];
        this.first = false;
        this.last = false;
      }

      if (_indexOf === arr.length - 1 && arr.length - 1 > 0) {
        this.prevId = arr[_indexOf - 1];
        this.last = true;
      }
      if (_indexOf === 0 && arr.length - 1 > 0) {
        this.first = true;
        this.nextId = arr[_indexOf + 1];
      }
      if (_indexOf === 0 && arr.length === 1) {
        this.first = true;
        this.last = true;
      }
    },
    getDataId() {
      //通过模块查询资讯id
      const that = this;
      this.$axios
        .post('/v1/information/queryInfoDynamicsIdList', {
          moduleType: "投资学堂"
        })
        .then(function(res) {
          console.log(res);
          that.lock = true;
          that.detailId = res.data.idList;
          that.getId(that.detailId, that.id);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    prevs() {
      if (this.first) {
        return;
      }
      if (this.lock) {
        this.lock = false;
        this.$router.push({
          name: "News_School_Detail",
          params: {
            id: this.prevId
          }
        });
        this.getData(this.prevId);
      }
    },
    next() {
      if (this.last) {
        return;
      }
      if (this.lock) {
        this.lock = false;
        this.$router.push({
          name: "News_School_Detail",
          params: {
            id: this.nextId
          }
        });

        this.getData(this.nextId);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.info {
  margin: 0 auto;
  .header {
    width: 100%;
    margin: 100px auto 30px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    font-size: 20px;
    color: #000000;
  }
  .tips {
    width: 100%;
    font-size: 13px;
    color: #cccccc;
    margin-bottom: 36px;
    line-height: 17px;
    height: 17px;
    text-align: center;
  }
  .content {
    width: 100%;
    padding: 50px 0px 100px;
    border-top: 1px solid #dadce0;
    border-bottom: 1px solid #dadce0;
  }
  .infoPageBox {
    margin: 40px auto 80px;
    .infoBtn {
      font-size: 13px;
      color: #cca876;
      width: 90px;
      height: 30px;
      box-sizing: border-box;
      border: 1px solid #cca876;
      cursor: pointer;
      .hd_back_right_icon {
        float: right;
        transform: rotate(90deg) translateX(2px);
        font-size: 13px;
      }
      .hd_back_left_icon {
        float: left;
        transform: rotate(-90deg) translateX(-2px);
        font-size: 13px;
      }
      &:hover {
        background: #cca876;
        color: #f0f0f0;
        .spc_color {
          color: #f0f0f0 !important;
        }
      }
      .icon-left {
        display: block;
        width: 52px;
        height: 16px;
        margin: 6px auto;
        overflow: hidden;
      }
      .icon-right {
        display: block;
        width: 52px;
        height: 16px;
        margin: 6px auto;
      }
    }
  }
}
</style>